<template>
  <div class="home">
    <table>
      <tr>
        <td>번호</td>
        <td>제목</td>
        <td>클릭횟수(조회수)</td>
      </tr>
      <tr 
        v-for="(글순서, i) in writelist" 
        :key="i"
      >
        <td>{{글순서.id}}</td>
        <td @click="addcount(글순서.id)">{{글순서.title}}</td>
        <td>{{글순서.count}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'HomeView',
  components: {
    
  },
  computed : {
    writelist : function() {
      return this.$store.state.list;
    }
  },
  methods: {
    addcount(id) {
      this.$router.push(`/board/${id}`)
      this.$store.state.list[id-1].count++;
    }
  }
}
</script>
