import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    list : [
      {id : 1, title : '첫번째 제목', memo: '첫내용', count : 0},
      {id : 2, title : '두번째', memo: '22내용', count: 0}
    ],

  },
  getters: {
  },
  mutations: {
    addMemo : function (state, Memobox) {
      const id = state.list.length +1;
      state.list.push({id: id , title: Memobox.title, memo : Memobox.memo, count : 0});
    }
  },
  actions: {
  },
  modules: {
  }
})
